function LicenseDatabase(props) {
	const { handlePage, Language } = props;
	const classes = useLDBStyle();
	const [search, setSearch] = useState('');
	const [rows, setRows] = useState([]);

	useEffect(() => {
		axios
			.post('https://mb_licensemanager/getLicensedPlayers', {})
			.then((response) => {
				setRows(response.data);
			});
	}, [Language]);

	const handleSearch = (event) => {
		setSearch(event.target.value);
	};

	return (
		<div className={classes.mainBox}>
			<Typography variant="h3" align="center" className={classes.primaryTitle}>
				{Language['title']}
			</Typography>
			<img
				alt="Logo LSPD"
				src="nui://mb_licensemanager/html/build/image/LSPD.png"
				style={{
					position: 'absolute',
					height: '10vh',
					top: '2vh',
					right: '2vw',
				}}
			/>
			<br />
			<hr className={classes.primary} />
			<TableContainer className={classes.tableContainer}>
				<Table className={classes.table} size="small" stickyHeader>
					<TableHead>
						<StyledTableRow>
							<StyledTableCell>{Language.header['number']}</StyledTableCell>
							<StyledTableCell>{Language.header['name']}</StyledTableCell>
							<StyledTableCell>{Language.header['licenses']}</StyledTableCell>
							<StyledTableCell>{Language.header['manage']}</StyledTableCell>
						</StyledTableRow>
					</TableHead>
					<TableBody>
						{rows
							.filter((row) =>
								[row.firstname, row.lastname].join(' ').includes(search)
							)
							.map((row, index) => {
								return (
									<StyledTableRow>
										<StyledTableCell>{index + 1}</StyledTableCell>
										<StyledTableCell>{`${row.firstname} ${row.lastname}`}</StyledTableCell>
										<StyledTableCell>{row.total}</StyledTableCell>
										<StyledTableCell>
											<Button
												variant="outlined"
												size="small"
												onClick={() => {
													handlePage('manage', row.identifier);
												}}
											>
												{Language['manage_button']}
											</Button>
										</StyledTableCell>
									</StyledTableRow>
								);
							})}
					</TableBody>
				</Table>
			</TableContainer>
			<div>
				<Grid
					container
					spacing={3}
					direction="row"
					justify="space-between"
					alignItems="center"
				>
					<Grid item>
						<Button
							variant="outlined"
							className={classes.danger}
							onClick={() => {
								handlePage('home');
							}}
						>
							{Language['back_button']}
						</Button>
					</Grid>
					<Grid item>
						<TextField
							id="standard-basic"
							label={Language['search_textfield']}
							onChange={handleSearch}
							className={classes.pid}
						/>
					</Grid>
				</Grid>
			</div>
		</div>
	);
}

