function CreateLicense(props) {
	const classes = useStyles();
	const { handlePage, setNotify, Language } = props;
	const [type, setType] = useState('');
	const [player, setPlayer] = useState({});
	const [licenses, setLicenses] = useState([]);

	const handleChange = (event) => {
		setType(event.target.value);
	};

	useEffect(() => {}, [Language]);

	const handlePidChange = (event) => {
		axios
			.post('https://mb_licensemanager/getPlayer', {
				id: event.target.value,
			})
			.then((response) => {
				setPlayer(response.data);
				setLicenses(response.data.licenses || []);
			});
	};

	const handleSubmit = (event) => {
		if (type === '' || player === {}) {
			setNotify({
				isOpen: true,
				message: Language['error_submit'],
				severity: 'error',
			});
		} else {
			axios
				.post('https://mb_licensemanager/createLicense', {
					player: player,
					license: type,
				})
				.then((response) => {
					if (response.data) {
						setNotify({
							isOpen: true,
							message: response.data,
							severity: 'success',
						});
						handlePage('home');
					}
				});
		}
	};

	return (
		<>
			<div className={classes.whiteBox}>
				<Typography variant="h3" className={classes.primaryTitle}>
					{Language['title']}
				</Typography>
				<br />
				<hr className={classes.primary} />
				<div className={classes.createSection}>
					<SecondaryTextField
						id="standard-basic"
						required
						label={Language.textfields['player_id']}
						type="number"
						onChange={handlePidChange}
						className={classes.pid}
					/>
					<FormControl className={classes.formControl}>
						<InputLabel id="simple-select-label">License Type</InputLabel>
						<Select
							labelId="demo-simple-select-helper-label"
							id="demo-simple-select-helper"
							value={type}
							onChange={handleChange}
							label={Language.textfields['license_type']}
						>
							{licenses.map((license) => {
								return (
									<MenuItem value={license.type}>{license.label}</MenuItem>
								);
							})}
						</Select>
					</FormControl>
				</div>
				<div className={classes.submitSection}>
					<Button
						variant="outlined"
						className={classes.success}
						size="large"
						onClick={handleSubmit}
					>
						{Language['submit_button']}
					</Button>
					<Button
						variant="outlined"
						size="large"
						className={classes.danger}
						onClick={() => {
							handlePage('home');
						}}
					>
						{Language['cancel_button']}
					</Button>
				</div>
			</div>
			<div className={classes.informationBox}>
				<div className={classes.marginBoxLeft}>
					<ThemeProvider theme={selectTheme}>
						<Grid
							container
							spacing={3}
							direction="row"
							justify="center"
							alignItems="center"
						>
							<Grid item xs={12} align="center">
								<Typography variant="h4">{Language['second_title']}</Typography>
								<hr
									style={{ marginLeft: '2vw', marginRight: '2vw' }}
									className={classes.primary}
								/>
							</Grid>

							<Grid item xs={6} align="center">
								<TextField
									id="standard-basic"
									disabled
									label={Language.informationfields['first_name']}
									InputLabelProps={{ shrink: true }}
									value={player.fname || ''}
								/>
							</Grid>
							<Grid item xs={6} align="center">
								<TextField
									id="standard-basic"
									disabled
									label={Language.informationfields['last_name']}
									InputLabelProps={{ shrink: true }}
									value={player.lname || ''}
								/>
							</Grid>
							<Grid item xs={6} align="center">
								<TextField
									id="standard-basic"
									disabled
									label={Language.informationfields['dob']}
									InputLabelProps={{ shrink: true }}
									value={player.dob || ''}
								/>
							</Grid>
							<Grid item xs={6} align="center">
								<TextField
									id="standard-basic"
									disabled
									label={Language.informationfields['height']}
									InputLabelProps={{ shrink: true }}
									value={player.height || ''}
								/>
							</Grid>
							<Grid item xs={6} align="center">
								<TextField
									id="outlined-basic"
									disabled
									label={Language.informationfields['sex']}
									value={player.sex ? player.sex.toUpperCase() : 'Male/Female'}
								/>
							</Grid>
						</Grid>
					</ThemeProvider>
					<img
						alt="Logo LSPD"
						src="nui://mb_licensemanager/html/build/image/LSPD.png"
						style={{ marginTop: 'auto', marginLeft: 'auto', height: '10vh' }}
					/>
				</div>
			</div>
		</>
	);
}

function ManageLicense(props) {
	const { handlePage, data, setNotify, Language } = props;
	const classes = useStyles();
	const [type, setType] = useState('');
	const [player, setPlayer] = useState({});
	const [licenses, setLicenses] = useState([]);

	const handleChange = (event) => {
		setType(event.target.value);
	};

	useEffect(() => {
		axios
			.post('https://mb_licensemanager/managePlayer', {
				identifier: data,
			})
			.then((response) => {
				setPlayer(response.data);
				setLicenses(response.data.licenses);
			});
	}, [Language, data]);

	const handleRevoke = (event) => {
		axios
			.post('https://mb_licensemanager/revoke', {
				identifier: data,
				license: type,
			})
			.then((response) => {
				if (response.data) {
					setNotify({
						isOpen: true,
						message: response.data,
						severity: 'success',
					});
					handlePage('home');
				}
			});
	};

	return (
		<>
			<div className={classes.whiteBox}>
				<Typography variant="h3" className={classes.primaryTitle}>
					{Language['title']}
				</Typography>
				<br />
				<hr className={classes.primary} />
				<div className={classes.createSection}>
					<FormControl className={classes.formControl}>
						<InputLabel id="simple-select-label">License Type</InputLabel>
						<Select
							labelId="demo-simple-select-helper-label"
							id="demo-simple-select-helper"
							value={type}
							onChange={handleChange}
							label={Language['license_type']}
						>
							{licenses.map((license) => {
								return (
									<MenuItem value={license.type}>{license.label}</MenuItem>
								);
							})}
						</Select>
					</FormControl>
				</div>
				<div className={classes.submitSection}>
					<Button
						variant="outlined"
						className={classes.danger}
						size="large"
						onClick={handleRevoke}
					>
						{Language['revoke_button']}
					</Button>
					<Button
						variant="outlined"
						size="large"
						onClick={() => {
							handlePage('home');
						}}
					>
						{Language['cancel_button']}
					</Button>
				</div>
			</div>
			<div className={classes.secondaryBox}>
				<div className={classes.marginBoxLeft}>
					<ThemeProvider theme={selectTheme}>
						<Grid
							container
							spacing={3}
							direction="row"
							justify="center"
							alignItems="center"
						>
							<Grid item xs={12} align="center">
								<Typography variant="h4">{Language['second_title']}</Typography>
								<hr
									style={{ marginLeft: '2vw', marginRight: '2vw' }}
									className={classes.primary}
								/>
							</Grid>

							<Grid item xs={6} align="center">
								<TextField
									id="standard-basic"
									disabled
									label={Language.informationfields['first_name']}
									InputLabelProps={{ shrink: true }}
									value={player.fname || ''}
								/>
							</Grid>
							<Grid item xs={6} align="center">
								<TextField
									id="standard-basic"
									disabled
									label={Language.informationfields['last_name']}
									InputLabelProps={{ shrink: true }}
									value={player.lname || ''}
								/>
							</Grid>
							<Grid item xs={6} align="center">
								<TextField
									id="standard-basic"
									disabled
									label={Language.informationfields['dob']}
									InputLabelProps={{ shrink: true }}
									value={player.dob || ''}
								/>
							</Grid>
							<Grid item xs={6} align="center">
								<TextField
									id="standard-basic"
									disabled
									label={Language.informationfields['height']}
									InputLabelProps={{ shrink: true }}
									value={player.height || ''}
								/>
							</Grid>
							<Grid item xs={6} align="center">
								<TextField
									id="outlined-basic"
									label={Language.informationfields['sex']}
									InputLabelProps={{ shrink: true }}
									value={player.sex ? player.sex.toUpperCase() : ''}
								/>
							</Grid>
						</Grid>
					</ThemeProvider>
					<img
						alt="Logo LSPD"
						src="nui://mb_licensemanager/html/build/image/LSPD.png"
						style={{ marginTop: 'auto', marginLeft: 'auto', height: '10vh' }}
					/>
				</div>
			</div>
		</>
	);
}

